import React, { useState, useEffect } from 'react';
import { Form, Row, Col, Input, Select, Icon, Radio, Button, Spin } from 'antd';
import uuidv4 from 'uuid/v4';
import { injectIntl } from 'react-intl';

import { getClients } from './functions';
import { getProcessTypes } from './../../services';

const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const radioStyle = {
  display: 'block',
  height: '30px',
  lineHeight: '30px',
};

const CreateUserCmp = ({
  loading,
  user,
  mode,
  users,
  userLogin,
  changeUser,
  createUser,
  form,
  form: { getFieldDecorator },
  intl: { messages },
}) => {
  const [confirmDirty, setConfirmDirty] = useState(false);
  const [process, setProcess] = useState([]);
  const [apikey, setApikey] = useState('');
  const [role, setRole] = useState('user');
  const [clients, setClients] = useState([]);
  const [fetching, setFetching] = useState(false);

  useEffect(() => {
    fetch();
    setClients(getClients(users).clients);
  }, []);

  const fetch = async () => {
    try {
      setFetching(true);
      const { data } = await getProcessTypes();
      setProcess(data);
      setFetching(false);
    } catch (error) {
      setFetching(false);
    }
  };

  const handleSubmit = e => {
    e.preventDefault();

    form.validateFields((err, values) => {
      if (!err) {
        const formData = {
          id: user.id || null,
          fullName: values.fullName,
          email: values.email,
          password: values.password,
          rol: role,
          typeOfPermits: values.typeOfPermits,
          typeOfUser: values.typeOfUser,
          apikey: values.apikey,
          process: values.process,
        };

        if (values.canTrain) {
          formData['canTrain'] = values.canTrain;
        }
        if (values.canClone) {
          formData['canClone'] = values.canClone;
        }

        if (role === 'user' && values.client) {
          formData.client = values.client;
        }

        createUser(formData, form);
      }
    });
  };

  const compareToFirstPassword = (rule, value, callback) => {
    if (value && value !== form.getFieldValue('password')) {
      callback(messages.signup.rules.password.match);
    } else {
      callback();
    }
  };

  const validateToNextPassword = (rule, value, callback) => {
    if (value && confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };

  const handleConfirmBlur = e => {
    const value = e.target.value;
    setConfirmDirty(confirmDirty || !!value);
  };

  const handleUserChange = value => {
    setRole(value);
  };

  const onSelectChange = value => {
    let cl = clients.filter(item => item.id === value);
    if (cl && cl.length) {
      let copyUser = user;
      copyUser.typeOfUser = cl[0].typeOfUser;
      changeUser(copyUser);
    }
  };

  let showPass = (
    <div>
      <FormItem label={messages.signup.password} hasFeedback>
        {getFieldDecorator('password', {
          rules: [
            {
              required: true,
              message: messages.signup.rules.password.required,
            },
            {
              validator: validateToNextPassword,
            },
          ],
        })(<Input autoComplete="new-password" disabled={mode === 'see'} type="password" />)}
      </FormItem>
      <FormItem label={messages.signup.confirm} hasFeedback>
        {getFieldDecorator('confirm', {
          rules: [
            {
              required: true,
              message: messages.signup.rules.password.confirm,
            },
            {
              validator: compareToFirstPassword,
            },
          ],
        })(<Input autoComplete="new-password" type="password" onBlur={handleConfirmBlur} />)}
      </FormItem>
    </div>
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 18 },
    },
  };

  return (
    <Spin spinning={fetching}>
      <Form {...formItemLayout} onSubmit={handleSubmit} autocomplete="off">
        <Row justify="center" type="flex">
          <Col xs={24}>
            <FormItem label={messages.signup.fullName} hasFeedback>
              {getFieldDecorator('fullName', {
                initialValue: user.fullName,
                rules: [
                  {
                    required: true,
                    message: messages.signup.rules.fullName.required,
                  },
                ],
              })(<Input disabled={mode === 'see'} type="text" />)}
            </FormItem>
            <FormItem label="E-mail" hasFeedback>
              {getFieldDecorator('email', {
                initialValue: user.email,
                rules: [
                  {
                    type: 'email',
                    message: messages.signup.rules.email.type,
                  },
                  {
                    required: true,
                    message: messages.signup.rules.email.required,
                  },
                ],
              })(<Input disabled={mode !== 'add'} />)}
            </FormItem>
            <FormItem label="APIkey">
              {getFieldDecorator('apikey', {
                initialValue: apikey,
              })(
                <Input
                  addonAfter={
                    <Icon
                      onClick={() => {
                        setApikey(uuidv4());
                      }}
                      style={{ cursor: 'pointer' }}
                      type="reload"
                    />
                  }
                />
              )}
            </FormItem>
            <FormItem label={messages.signup.role} hasFeedback>
              <Select
                defaultValue={role}
                disabled={userLogin.rol === 'client'}
                onChange={handleUserChange}
              >
                <Option value="user">{messages.signup.roles.user}</Option>
                <Option value="client">{messages.signup.roles.client}</Option>
                {/* <Option value="admin">{messages.signup.roles.admin}</Option> */}
              </Select>
            </FormItem>

            {userLogin.rol === 'admin' && (
              <FormItem label="Process Types" hasFeedback>
                {getFieldDecorator('process', {
                  rules: [
                    {
                      required: true,
                    },
                  ],
                })(
                  <Select mode="multiple" placeholder="Please select" style={{ width: '100%' }}>
                    {process.map(item => (
                      <Option value={item.id}>{item.name}</Option>
                    ))}
                  </Select>
                )}
              </FormItem>
            )}

            {role === 'user' && userLogin.rol === 'admin' && (
              <FormItem label={messages.signup.roles.client} hasFeedback>
                {getFieldDecorator('client', {
                  rules: [
                    {
                      required: true,
                    },
                  ],
                })(
                  <Select
                    showSearch
                    placeholder="Select a client"
                    optionFilterProp="children"
                    onChange={onSelectChange}
                    filterOption={(input, option) =>
                      option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                    }
                  >
                    {clients.length &&
                      clients.map(item => {
                        return (
                          <Option
                            key={item.id}
                            value={item.id}
                          >{`${item.email} / ${item.apikey}`}</Option>
                        );
                      })}
                  </Select>
                )}
              </FormItem>
            )}

            <FormItem label={messages.signup.typeOfUser}>
              {getFieldDecorator('typeOfUser', {
                initialValue: user.typeOfUser,
                rules: [
                  {
                    required: true,
                  },
                ],
              })(
                <RadioGroup disabled={userLogin.rol === 'client'}>
                  <Radio style={radioStyle} value={1}>
                    {messages.signup.typeOfUsers.nqnp}
                  </Radio>
                  <Radio style={radioStyle} value={2}>
                    {messages.signup.typeOfUsers.qnp}
                  </Radio>
                  <Radio style={radioStyle} value={3}>
                    {messages.signup.typeOfUsers.qp}
                  </Radio>
                </RadioGroup>
              )}
            </FormItem>

            {showPass}

            {userLogin.rol === 'client' && (
              <React.Fragment>
                <Form.Item label="Can Train">
                  {getFieldDecorator('canTrain', {
                    initialValue: 0,
                  })(
                    <Radio.Group>
                      <Radio value={1}>Yes</Radio>
                      <Radio value={0}>No</Radio>
                    </Radio.Group>
                  )}
                </Form.Item>
                <Form.Item label="Can Clone">
                  {getFieldDecorator('canClone', {
                    initialValue: 0,
                  })(
                    <Radio.Group>
                      <Radio value={1}>Yes</Radio>
                      <Radio value={0}>No</Radio>
                    </Radio.Group>
                  )}
                </Form.Item>
              </React.Fragment>
            )}
            <Form.Item className="button-user-form">
              <Button loading={loading} style={{ float: 'right' }} type="primary" htmlType="submit">
                {messages.default.create}
              </Button>
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Spin>
  );
};

const CreateUser = Form.create()(injectIntl(CreateUserCmp));

export default CreateUser;
