import React, { useEffect, useState } from 'react';
import { Form, Row, Col, Input, Select, Radio, Button, Spin } from 'antd';
import { injectIntl } from 'react-intl';

import { getProcessTypes } from './../../services';

const FormItem = Form.Item;
const Option = Select.Option;
const RadioGroup = Radio.Group;

const radioStyle = {
  display: 'block',
  height: '30px',
  lineHeight: '30px',
};

const EditUserFormCmp = ({
  ra,
  loading,
  user,
  mode,
  userLogin,
  updateUser,
  form,
  form: { getFieldDecorator },
  intl: { messages },
}) => {
  const [process, setProcess] = useState([]);
  const [apikey, setApikey] = useState('');
  const [role, setRole] = useState('user');
  const [fetching, setFetching] = useState(false);
  const [processDefaults, setProcessDefaults] = useState(false);

  useEffect(() => {
    fetch();
    setApikey(user.apikey);
    setRole(user.rol);

    const dd = user.processTypes.map(item => item.id);
    setProcessDefaults(dd);
  }, [ra]);

  const fetch = async () => {
    try {
      setFetching(true);
      const { data } = await getProcessTypes();
      setProcess(data);
      setFetching(false);
    } catch (error) {
      setFetching(false);
    }
  };

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 18 },
    },
  };

  const handleSubmit = e => {
    e.preventDefault();

    form.validateFields((err, values) => {
      if (!err) {
        const formData = {
          id: user.id || null,
          fullName: values.fullName,
          email: values.email,
          password: values.password,
          rol: role,
          typeOfUser: values.typeOfUser,
          apikey: values.apikey,
          process: values.process,
        };

        if (values.canTrain) {
          formData['canTrain'] = values.canTrain;
        }
        if (values.canClone) {
          formData['canClone'] = values.canClone;
        }

        if (role === 'user' && values.client) {
          formData.client = values.client;
        }

        updateUser(formData, form);
      }
    });
  };

  return (
    <Spin spinning={fetching}>
      <Form {...formItemLayout} onSubmit={handleSubmit} autocomplete="off">
        <Row justify="center" type="flex">
          <Col xs={24}>
            <FormItem label={messages.signup.fullName} hasFeedback>
              {getFieldDecorator('fullName', {
                initialValue: user.fullName,
                rules: [
                  {
                    required: true,
                    message: messages.signup.rules.fullName.required,
                  },
                ],
              })(<Input disabled={mode === 'see'} type="text" />)}
            </FormItem>
            <FormItem label="E-mail" hasFeedback>
              {getFieldDecorator('email', {
                initialValue: user.email,
                rules: [
                  {
                    type: 'email',
                    message: messages.signup.rules.email.type,
                  },
                  {
                    required: true,
                    message: messages.signup.rules.email.required,
                  },
                ],
              })(<Input disabled={mode !== 'add'} />)}
            </FormItem>
            <FormItem label="APIkey">
              {getFieldDecorator('apikey', {
                initialValue: apikey,
              })(<Input disabled />)}
            </FormItem>
            {userLogin.rol === 'admin' && (
              <FormItem label="Process Types" hasFeedback>
                {getFieldDecorator('process', {
                  initialValue: processDefaults,
                  rules: [
                    {
                      required: true,
                    },
                  ],
                })(
                  <Select mode="multiple" placeholder="Please select" style={{ width: '100%' }}>
                    {process.map(item => (
                      <Option value={item.id}>{item.name}</Option>
                    ))}
                  </Select>
                )}
              </FormItem>
            )}

            <FormItem label={messages.signup.role} hasFeedback>
              {getFieldDecorator('rol', {
                initialValue: user.rol,
                rules: [
                  {
                    required: true,
                  },
                ],
              })(
                <Select disabled>
                  <Option value="user">{messages.signup.roles.user}</Option>
                  <Option value="client">{messages.signup.roles.client}</Option>
                  {/* <Option value="admin">{messages.signup.roles.admin}</Option> */}
                </Select>
              )}
            </FormItem>

            <FormItem label={messages.signup.typeOfUser}>
              {getFieldDecorator('typeOfUser', {
                initialValue: user.typeOfUser,
                rules: [
                  {
                    required: true,
                  },
                ],
              })(
                <RadioGroup disabled={userLogin.rol === 'client' || user.UserId}>
                  <Radio style={radioStyle} value={1}>
                    {messages.signup.typeOfUsers.nqnp}
                  </Radio>
                  <Radio style={radioStyle} value={2}>
                    {messages.signup.typeOfUsers.qnp}
                  </Radio>
                  <Radio style={radioStyle} value={3}>
                    {messages.signup.typeOfUsers.qp}
                  </Radio>
                </RadioGroup>
              )}
            </FormItem>

            {userLogin.rol === 'client' && (
              <React.Fragment>
                <Form.Item label="Can Train">
                  {getFieldDecorator('canTrain', {
                    initialValue: 0,
                  })(
                    <Radio.Group>
                      <Radio value={1}>Yes</Radio>
                      <Radio value={0}>No</Radio>
                    </Radio.Group>
                  )}
                </Form.Item>
                <Form.Item label="Can Clone">
                  {getFieldDecorator('canClone', {
                    initialValue: 0,
                  })(
                    <Radio.Group>
                      <Radio value={1}>Yes</Radio>
                      <Radio value={0}>No</Radio>
                    </Radio.Group>
                  )}
                </Form.Item>
              </React.Fragment>
            )}
            <Form.Item className="button-user-form">
              <Button loading={loading} style={{ float: 'right' }} type="primary" htmlType="submit">
                {messages.default.save}
              </Button>
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Spin>
  );
};

const EditUserForm = Form.create()(injectIntl(EditUserFormCmp));

export default EditUserForm;
